.CartItem {
  width: 100%;
  display: flex;
  min-height: 80px;
  margin-bottom: 15px;
  font-family: "Lato", sans-serif;

  img {
    width: 27%;
  }

  .item-details {
    width: 70%;
    display: block;
    color: black;
    padding-right: 20px;
    margin: auto 0;

    .remove-from-cart {
      cursor: pointer;
      display: inline-flex;
      i {
        font-size: 1rem;
      }
    }
  }

  .name {
    margin-left: 5px;
  }

  .price {
    margin-right: 8px;
  }

  .name,
  .price {
    font-size: 0.8rem;
  }
}
